*
{
    margin:0;
    padding:0;
}
body 
{
    background: url("http://superdevresources.com/wp-content/uploads/2014/02/pattern-background.png");
    font-family: "Bree Serif", serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
} 
  .heading
  {
    text-align: center;
    justify-content: center;
    color: #f71aa2;
  
  }
  .App
  {
    background: linear-gradient(83deg,#f71aa2 0%, #6800ff 80%);
    width: 100%;
    min-height: 200px;
    height: min-content;
    border-radius: 15px;
    padding: 25px;
    box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
    display: flex;
    justify-content: space-evenly;
    margin-right: 20px;
  }

.Question-Section
{
position: relative;
width: 100%;
}
.Question-count
{
    color:blue;
    font-size:17px;
    margin-bottom: 20px;
}
.Question-count span{
    font-size: 40px;
    color:red;
} 
.Question-Text
{
    color: white;
    width: 100%;
    font-size:20px;
    font-weight: bold;

}
.Answer-Section
{
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 50%;
  justify-content: space-between;
}
.button {
    width:80%;
    font-size: 15px;
    color: #ffffff;
    background: linear-gradient(83deg,#f71aa2 0%, #6800ff 80%);
    border-radius: 25px;
    display: flex;
    padding:10px;
    justify-content: flex-start;
    border: 3px solid #234668;
    align-items:center ;
    cursor: pointer;
    margin-left: auto;
  }
  
.button:hover {
    background-color: #1b295c;
  }
.score-section
{
  display: flex;
  align-items: center;
  font-size: 30px;
  color:white;
  font-weight: bold;

} 
.Reset{
  margin-top: auto;
  text-align: center;
  padding:10px;
  font-weight: bold;
  border-radius: 25px;
  border: 3px solid #234668;
  cursor: pointer;
  font-size: 15px;
  color: #ffffff;
  background: linear-gradient(83deg,#f71aa2 0%, #6800ff 80%);
  border-radius: 25px;
}
